import React, { useState } from 'react';
import './App.css';
import pluralize from 'pluralize';

const unitsData: Record<string, Record<string, number>> = {
  length: {
    meter: 1,
    kilometer: 1000,
    mile: 1609.34,
    inch: 0.0254,
    centimeter: 0.01,
    'average duck': 0.3,
  },
  weight: {
    kilogram: 1,
    gram: 0.001,
    pound: 0.453592,
    ounce: 0.0283495,
    'average elephant': 5000,
  },
  // Add more categories and units as needed
};

const App: React.FC = () => {
  const [inputValue, setInputValue] = useState<number>(0);
  const [selectedCategory, setSelectedCategory] = useState<string>('length');
  const [unit1, setUnit1] = useState<string>('meter');
  const [unit2, setUnit2] = useState<string>('kilometer');

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setInputValue(parseFloat(e.target.value));
  };

  const handleCategoryChange = (e: React.ChangeEvent<HTMLSelectElement>) => {
    setSelectedCategory(e.target.value);
    setUnit1(Object.keys(unitsData[e.target.value])[0]);
    setUnit2(Object.keys(unitsData[e.target.value])[1]);
  };

  const handleUnit1Change = (e: React.ChangeEvent<HTMLSelectElement>) => {
    setUnit1(e.target.value);
  };

  const handleUnit2Change = (e: React.ChangeEvent<HTMLSelectElement>) => {
    setUnit2(e.target.value);
  };

  const convertUnits = () => {
    const conversionRate =
      unitsData[selectedCategory][unit1] / unitsData[selectedCategory][unit2];
    return inputValue * conversionRate;
  };

  const converted = convertUnits();
  const convertedFormatted = converted < 0.01 ? String(converted) : converted.toFixed(2).replace(/\.?0+$/, "");

  return (
    <div className="App">
      <h1>Compare Sizes</h1>
      <div>
        <label>
          Input Value:
          <input type="number" value={inputValue} onChange={handleInputChange} />
        </label>
      </div>
      <div>
        <label>
          Select Category:
          <select value={selectedCategory} onChange={handleCategoryChange}>
            {Object.keys(unitsData).map(category => (
              <option key={category} value={category}>
                {category}
              </option>
            ))}
          </select>
        </label>
      </div>
      <div>
        <label>
          Select Unit 1:
          <select value={unit1} onChange={handleUnit1Change}>
            {Object.keys(unitsData[selectedCategory]).map(unit => (
              <option key={unit} value={unit}>
                {unit}
              </option>
            ))}
          </select>
        </label>
        <label>
          Select Unit 2:
          <select value={unit2} onChange={handleUnit2Change}>
            {Object.keys(unitsData[selectedCategory]).map(unit => (
              <option key={unit} value={unit}>
                {unit}
              </option>
            ))}
          </select>
        </label>
      </div>
      <div>
        <p>
          {inputValue} {pluralize(unit1, inputValue)}
          {' '} is equal to {' '}
          {convertedFormatted} {pluralize(unit2, converted)}
        </p>
      </div>
    </div>
  );
};

export default App;
